@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ededf5;
}

body,
input,
button {
  font-family: "Roboto", sans-serif;
}

button {
  border-radius: 5px;
  border: 1px solid #31d2f7;
  font-weight: bold;
  outline: 0;
  transition: 0.3s ease;
}

button:not([disabled]) {
  cursor: pointer;
}

.list-form {
  width: 100%;

  margin: 15px 0;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}

.list-form input {
  flex: 1 1;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #d5d8da;
  padding: 15px;
  outline: 0;
}

.list-form button {
  height: 40px;
  padding: 0 20px;
  color: #fff;
  background-color: #31d2f7;
}

.list-form button:hover {
  background-color: #69eaff;
  border-color: #69eaff;
}

.list-of-items {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  padding-bottom: 30px;
}

.list-of-items li {
  width: 100%;
  max-width: 100%;
  word-wrap: break-word;
  word-break: break-all;
  min-height: 60px;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.list-of-items li.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.list-of-items li.disabled button {
  cursor: not-allowed;
}

.list-of-items li button {
  min-width: 100px;
  background-color: #fff;
  color: #31d2f7;
  padding: 5px;
  margin-left: 30px;
}

.list-of-items li button:not([disabled]):hover {
  background-color: #31d2f7;
  color: #fff;
}

.container {
  width: 100%;
  max-width: 600px;
  padding: 0 15px;
  margin: 0 auto;
}

.container h1 {
  background-color: rgb(13, 5, 87);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  font-size: 20px;
  color: #fff;
  padding: 20px;
}

